import { NgModule } from '@angular/core';
import { CoreModule, HOOK_DOCS } from '@c8y/ngx-components';
import { ConnectSmartphoneFactory } from './connect-smartphone.factory';
import { SensorPhoneModalComponent } from './sensor-phone-modal.component';
import { QRCodeModule } from 'angularx-qrcode';
import { SensorPhoneService } from './sensor-phone.service';

@NgModule({
  imports: [CoreModule, QRCodeModule],
  declarations: [SensorPhoneModalComponent],
  entryComponents: [SensorPhoneModalComponent],
  providers: [
    {
      provide: HOOK_DOCS,
      useClass: ConnectSmartphoneFactory,
      multi: true
    },
    SensorPhoneService
  ]
})
export class SensorPhoneModule {}
