import { Injectable } from '@angular/core';
import { DocLinkFactory, OptionsService, gettext, DocLink } from '@c8y/ngx-components';
import { of } from 'rxjs';
import { SensorPhoneService } from './sensor-phone.service';

@Injectable()
export class ConnectSmartphoneFactory implements DocLinkFactory {
  constructor(
    private optionsService: OptionsService,
    private sensorPhoneService: SensorPhoneService
  ) {}
  get() {
    if (this.optionsService.get('sensorPhone', false)) {
      return of([
        {
          icon: 'c8y-mobile-add',
          type: 'quicklink',
          label: gettext('Connect smartphone'),
          url: undefined,
          click: () => this.sensorPhoneService.createModal()
        } as DocLink
      ]);
    }
  }
}
