import { Component, ViewChild, AfterViewInit, ElementRef, Renderer2 } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap';
import { C8yStepper, gettext } from '@c8y/ngx-components';
import { SensorPhoneService } from './sensor-phone.service';
import { throttleTime } from 'rxjs/operators';
import { Realtime, DeviceRegistrationService } from '@c8y/client';
import { Router } from '@angular/router';
import { get } from 'lodash-es';
import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';

@Component({
  selector: 'c8y-sensor-phone',
  templateUrl: 'sensor-phone-modal.component.html'
})
export class SensorPhoneModalComponent implements AfterViewInit {
  @ViewChild(C8yStepper, { static: false })
  stepper: C8yStepper;
  @ViewChild(C8yStepper, { read: ElementRef, static: false })
  container: ElementRef;
  pendingDeviceBootstrap: boolean = true;
  success;
  errorMessage = gettext('Failed to register a smartphone.');
  registrationData = null;
  tenantId;
  instance;
  deviceId: string;
  dashboardLink: string = '';
  private bootStrapObservable: Subscription;
  private managedObjectObservable: Subscription;

  constructor(
    private bsModalRef: BsModalRef,
    private sensorPhoneService: SensorPhoneService,
    private renderer: Renderer2,
    private deviceRegistrationService: DeviceRegistrationService,
    private realTime: Realtime,
    private router: Router
  ) {}

  ngAfterViewInit() {
    this.stepper.selectionChange
      .pipe(throttleTime(100))
      .subscribe((partialStepper: Partial<C8yStepper>) => this.bootstrapDevice(partialStepper));
    this.tenantId = this.sensorPhoneService.getTenantName();
    this.instance = this.sensorPhoneService.getHost();
  }

  async bootstrapDevice(stepper: Partial<C8yStepper>) {
    const { selectedIndex } = stepper;
    if (selectedIndex && selectedIndex === 2) {
      let device;
      this.deviceId = this.generateDeviceId();
      this.bootStrapObservable = this.realTime
        .observable('/bootstrap')
        .pipe(
          filter(
            status =>
              get(status, 'data.id') === this.deviceId && get(status, 'realtimeAction') === 'UPDATE'
          )
        )
        .subscribe(status => {
          this.pendingDeviceBootstrap = false;
          this.onDeviceStatusChanged(this.deviceId);
          this.bootStrapObservable.unsubscribe();
        });
      try {
        const group = await this.sensorPhoneService.addPhoneGroup();
        if (group) {
          device = { id: this.deviceId, groupId: group.id };

          this.registrationData = this.sensorPhoneService.generateRegistrationData(this.deviceId);

          const { data, res } = await this.deviceRegistrationService.create(device);
          if (res.status !== this.sensorPhoneService.HTTP_STATUS_CODE.CREATED) {
            this.onFailure();
          }
        }
      } catch (error) {
        this.onFailure();
      }
    }
  }

  generateDeviceId() {
    return 'phone' + Math.floor(Math.random() * 100000);
  }

  async onDeviceStatusChanged(deviceId) {
    let deviceMo;
    let dashboardMo;
    this.managedObjectObservable = this.realTime
      .observable(`/managedobjects/*`)
      .pipe(
        filter(
          status =>
            get(status, 'data.owner') === `device_${deviceId}` &&
            get(status, 'realtimeAction') === 'CREATE'
        )
      )
      .subscribe(response => {
        const mo = response.data;
        if (mo.type === 'c8y_SensorPhone') {
          deviceMo = mo;
        }
        if (mo.c8y_Dashboard) {
          dashboardMo = mo;
        }
        if (deviceMo && dashboardMo) {
          this.onManagedObjectCreated(deviceMo, dashboardMo);
          this.managedObjectObservable.unsubscribe();
        }
      });

    try {
      const { res } = await this.deviceRegistrationService.accept(deviceId);
      if (res.status !== this.sensorPhoneService.HTTP_STATUS_CODE.OK) {
        this.onFailure();
      }
    } catch (error) {
      this.onFailure();
    }
  }

  onManagedObjectCreated(deviceMo, dashboardMo) {
    const { id } = dashboardMo;
    this.dashboardLink = `/device/${deviceMo.id}/dashboard/${id}`;
    this.stepper.next();
    if (this.container) {
      this.renderer.setStyle(this.container.nativeElement, 'display', 'none');
      this.success = true;
    }
  }

  openDashboard() {
    if (this.dashboardLink) {
      this.router.navigate([this.dashboardLink]);
      this.cancel();
    }
  }

  cancel() {
    this.bsModalRef.hide();
    this.deleteDeviceRequest();
    this.cleanSubscription();
  }

  retry() {
    this.success = undefined;
    if (this.container) {
      this.renderer.setStyle(this.container.nativeElement, 'display', 'block');
    }
    this.deleteDeviceRequest();
    this.cleanSubscription();
    this.stepper.selectedIndex = 2;
  }

  onFailure() {
    this.success = false;
    this.stepper.next();
    if (this.container) {
      this.renderer.setStyle(this.container.nativeElement, 'display', 'none');
    }
  }

  async deleteDeviceRequest() {
    if (this.deviceId) {
      try {
        const { data, res } = await this.deviceRegistrationService.delete(this.deviceId);
      } catch (error) {
        //
      }
    }
  }

  cleanSubscription() {
    if (this.bootStrapObservable && !this.bootStrapObservable.closed) {
      this.bootStrapObservable.unsubscribe();
    }
    if (this.managedObjectObservable && !this.managedObjectObservable.closed) {
      this.managedObjectObservable.unsubscribe();
    }
  }

  ngOnDestroy() {
    this.cleanSubscription();
  }
}
